@mixin fontFontRemaster() {
	--fontSize: 14px;
	--lineHeightMod: 8px;
	--lineHeight: calc(var(--fontSize) + var(--lineHeightMod));
	--fontWeight: 400;
	--fontFontColor: var(--theme-paragraph);

	font-size: var(--fontSize);
	font-weight: var(--fontWeight);
	line-height: var(--lineHeight);
	color: var(--fontFontColor); }

//button
//Определение параметров кнопки
//colorButton - цвет текста кнопки
//backgroundColor - цвета фона
//backgroundColorHover - цвет фона при наведении
//colorHover - цвет текста при наведении
//borderColor - цвет рамки
//borderRadius - закругление рамки
//cursor - изменение курсора при наведении
//width - ширина
//height - высота

@mixin button() {
	--colorButton: var(--color-white);
	--backgroundColor: transparent;
	--backgroundColorHover: var(--color-pink);
	--colorHover: var(--color-white);
	--borderColor: var(--backgroundColor);
	--borderRadius: 40px;
	--cursor: pointer;
	--width: auto;
	--height: 40px;

	display: inline-flex;
	justify-content: center;
	align-items: center;
	height: var(--height);
	@include fontFontRemaster();
	font-family: 'Roboto', sans-serif;
	font-size: 16px;
	color: var(--colorButton);
	border-radius: var(--borderRadius);
	cursor: var(--cursor);
	background-color: var(--backgroundColor);
	border: 1px solid var(--borderColor);
	width: var(--width);
	transition: color 0.25s linear .125s, border .25s linear .125s, background-color .25s linear .125s;

	&:hover {
		background-color: var(--backgroundColorHover);
		color: var(--colorHover); } }
